.account-chooser {
    .panel.panel-default {
        border-width: 2px;
    }

    .panel.panel-default:hover {
        -webkit-box-shadow: 1px 5px 7px rgba(0, 0, 0, 0.15);
        box-shadow: 1px 5px 7px rgba(0, 0, 0, 0.15);
    }

    panel-heading .button {
        cursor:default;
        color:#333!important;
    }

    
        [class*=cnjnctn-type-]>[class*=cnjnctn-col]:not(:first-child):before {
            border-color: #6f6f6f;
            border-style: solid;
            box-sizing: content-box;
            font-size: 16px;
            font-weight: 600;
            height: 1.8em;
            left: auto;
            line-height: 1.7em;
            margin-top: -3.8em!important;
            padding: 0.3em;
            position: absolute;
            text-align: center;
            width: 1.8em;
            background-color: #fff;
            margin: 0 45%;
        }
    
        [class*=cnjnctn-type-] > [class*=cnjnctn-col]:first-child:after {
            background-color: #6f6f6f;
            background-size: 3px;
            content: " ";
            width: 100%;
            height: 3px !important;
            left: 0;
            position: absolute;
            bottom: -35px;
        }
    
        [class*=cnjnctn-type-]>[class*=cnjnctn-col]:not(:first-child):after {
            background: none;
            content: none;
            height: 0;
            position: relative;
            width: 0;
        }
    
    
}
